    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  body > main > .content-container > h1 {
    font-size: 2rem;
    margin-bottom: 2vh;
  }
  body > main > .content-container > p {
