@media only screen and (max-width: 800px) {
  body > main {
    width: 90vw;
    padding-top: 7vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    grid-gap: 3vw 3vh;
  }
  body > main > .img-container {
    width: 100%;
    -webkit-box-shadow: 5px 5px 5px 1px gray;
    box-shadow: 5px 5px 5px 1px gray;
  }
  body > main > .img-container > img {
    width: 100%;
    height: 100%;
  }
  body > main > .content-container {
    width: 90%;
    height: 40%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  body > main > .content-container > h1 {
    font-size: 1.5rem;
    margin-bottom: 2vh;
  }
  body > main > .content-container > p {
    font-size: 1rem;
    margin-bottom: 2vh;
  }
}
.overl {
  position: absolute;
  top: 0;
  left: 0;
}

.o-active {
  width: 100vw;
  height: 100vh;
  background: white;
  pointer-events: all;
  opacity: 0.5;
  z-index: 1;
}

#button {
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: #E4C1AE;
  width: 120px;
  height: 50px;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  -webkit-box-shadow: 1px 1px 2px 1px gray;
  box-shadow: 1px 1px 2px 1px gray;
}
#button:hover {
  color: black;
  transform: scale(1.05);
}
#button:active {
  color: #8b0000;
  background: white;
  transform: scale(1.1);
}

.btnX {
  font-size: 1.5rem;
  outline: none;
  border: none;
  background: none;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.reset {
  visibility: hidden;
  -webkit-transform: scale(0);
  transform: scale(0);
  color: #8b0000;
  cursor: pointer;
  border: none;
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: #E4C1AE;
  width: auto;
  height: 40px;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  -webkit-box-shadow: 1px 1px 2px 1px gray;
  box-shadow: 1px 1px 2px 1px gray;
  -webkit-transform: scale(1);
  margin-left: 3vw;
}

.r-active {
  visibility: visible;
  transform: scale(1);
}
.r-active:hover {
  color: black;
  transform: scale(1.05);
}
.r-active:active {
  color: #8b0000;
  background: white;
  transform: scale(1.1);
}

.popup {
  visibility: hidden;
  width: 40vw;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  -webkit-box-shadow: 0px 0px 7px 1px gray;
  box-shadow: 0px 0px 7px 1px gray;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: left;
  -ms-flex-align: left;
  align-items: left;
  line-height: 1.6;
  padding: 2% 5% 2% 5%;
}
.popup > h2 {
  font-size: 2rem;
}
.popup > p {
  font-size: 1rem;
}
.popup > p > span {
  color: #8b0000;
  font-size: 1.5rem;
  font-weight: 700;
}

.active {
  visibility: visible !important;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
}

html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

body {
  background-color: #FFFFFF;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}